* {
    box-sizing: border-box;
    font-family: 'Overpass', sans-serif;
    /*font-family: 'Roboto Mono', m onos pace, serif;*/
}

html {
    height: 100%;
}

body {
    height: 100%;
    margin: 0;
    overflow: hidden;
    font-size: 100%;
}

#root {
    height: 100%;
}

.container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.container-window {
    background: #ece7e3;
    width: -moz-fit-content;
    width: fit-content;
    padding: 50px;
    border-radius: 10px;
    box-shadow: 0 0 18px 2px black;
}

.round {
    border-radius: 10px;
}

.button {
    background: #af6559;
    color: white;
    border: none;
    border-radius: 10px;
    padding: 10px 15px;
}

.button:hover {
    opacity: 0.8;
    cursor: pointer;
}

.button:active {
    border: 1px solid #241916;
}

.menu {
    position: fixed;
    right: 0;
    top: 45px;
    background: white;
    width: 200px;
    height: fit-content;
}

.menu hr , button {
    margin: 0;
}

.menu-button {
    width: 100%;
    height: 45px;
    margin: 0;
    border-radius: 0;
    border: none;
    cursor: pointer;
    color: #241916;
    text-align: center;
}

.menu-button:hover {
    background: #2c3749;
    color: white;
}

svg {
    height: 100%;
    width: 100%;
    margin: 0 0 0 0;
}

.icon-button {
    height: 25px;
    width: 25px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #899ebd;
    border: none;
    border-radius: 2px;
}

.icon-button:hover {
    opacity: 0.8;
    cursor: pointer;
}

hr {
   width: 100%;
}

p, div, button, input, select {
        font-size: 1.011em;
    }

